*, :before, :after {
  box-sizing: border-box;
}

html {
  text-size-adjust: none;
  vertical-align: baseline;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
  margin-block-end: 0;
  padding: 0;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

body {
  min-height: 100vh;
  line-height: 1.5;
}

h1, h2, h3, h4 {
  text-wrap: balance;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:target {
  scroll-margin-block: 5ex;
}

:root {
  --spacing: .125rem;
  --spacing-00: 0px;
  --spacing-01: var(--spacing);
  --spacing-02: calc(var(--spacing) * 2);
  --spacing-03: calc(var(--spacing) * 3);
  --spacing-04: calc(var(--spacing) * 4);
  --spacing-05: calc(var(--spacing) * 6);
  --spacing-06: calc(var(--spacing) * 8);
  --spacing-07: calc(var(--spacing) * 10);
  --spacing-08: calc(var(--spacing) * 12);
  --spacing-09: calc(var(--spacing) * 16);
  --spacing-10: calc(var(--spacing) * 20);
  --spacing-11: calc(var(--spacing) * 24);
  --spacing-12: calc(var(--spacing) * 28);
  --spacing-13: calc(var(--spacing) * 32);
  --spacing-14: calc(var(--spacing) * 36);
  --spacing-15: calc(var(--spacing) * 40);
  --spacing-16: calc(var(--spacing) * 48);
  --spacing-17: calc(var(--spacing) * 60);
  --spacing-18: calc(var(--spacing) * 80);
  --spacing-zero: var(--spacing-00);
  --spacing-xs: var(--spacing-02);
  --spacing-s: var(--spacing-04);
  --spacing-m: var(--spacing-05);
  --spacing-l: var(--spacing-06);
  --spacing-xl: var(--spacing-08);
  --spacing-xxl: var(--spacing-09);
  --spacing-xxxl: var(--spacing-11);
  --size-xs: var(--spacing-06);
  --size-s: var(--spacing-08);
  --size-m: var(--spacing-09);
  --controls-size-default: var(--spacing-09);
  --controls-size-small: var(--spacing-08);
}

