:root {
  --dialog-strong: 0px 16px 48px #00000029;
  --dialog: 0px 0px 0px .5px #0000000f, 0px 12px 24px #00000014, 0px 2px 3px #0000000a;
  --content: 0px 2px 3px #0000000f;
  --canvas: 0px 0px 0px #0000;
  --keyboard-key: 0 2px 0px var(--grey4);
  --focus-default: 0px 0px 0px 2px var(--blue5);
  --focus-accent: 0px 0px 0px 2px var(--blue8);
  --focus-inverse: 0px 0px 0px 2px var(--white);
  --radius-xs: .125rem;
  --radius-sm: .25rem;
  --radius-md: .375rem;
  --radius-lg: .5rem;
  --radius-xl: .75rem;
  --radius-2xl: 1rem;
  --radius-3xl: 1.5rem;
  --radius-4xl: 2rem;
  --blur-xs: .25rem;
  --blur-sm: .5rem;
  --blur-md: .75rem;
  --blur-lg: 1rem;
  --blur-xl: 1.5rem;
  --blur-2xl: 2.5rem;
  --blur-3xl: 4rem;
  --perspective-dramatic: 6.25rem;
  --perspective-near: 18.75rem;
  --perspective-normal: 31.25rem;
  --perspective-midrange: 50rem;
  --perspective-distant: 75rem;
  --aspect-video: 16 / 9;
  --default-transition-duration: .15s;
  --default-transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

